import React from "react";

const SlideIcon = props => {
    switch (props.slide) {
        case 1: return <div className="slide-icon">{icon1}</div>;
        case 2: return <div className="slide-icon">{icon2}</div>;
        case 3: return <div className="slide-icon">{icon3}</div>;
        case 4: return <div className="slide-icon">{icon4}</div>;
        case 5: return <div className="slide-icon">{icon5}</div>;
        case 6: return <div className="slide-icon">{icon6}</div>;
        default: return <div className="slide-icon"/>
    }
}

export default SlideIcon;

